import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import { Link } from '@vkontakte/vkui';


const textAlign = {
	"textAlign": "center",
	"paddingTop": "40px"
};

const linkButton = {
	"background": "#ffffff none repeat scroll 0 0",
	"border": "1px solid #4BB34A",
	"padding": "20px",
	"color": "#4BB34A",
    "outline": "medium none",  
    "textDecoration": "none"
}

const Home = ({ id, go, fetchedUser, vkTopic }) => (
	<Panel id={id}>
		<PanelHeader>Physical Transformation</PanelHeader>
		<Div style={ textAlign }>
			<Link href="https://physical-transformation.online/" target="_blank" style={ linkButton }>Заполнить анкету</Link>
			{fetchedUser && <Link href={ vkTopic } target="_blank" style={ linkButton }>Твоя тема</Link>}
		</Div>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		id: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
